body {
  background-color: #fcfcfd;
}

.background {
  background-image: linear-gradient(115deg, $main-color 77%, #fcfcfd 77%);
  width: 100%;
  height: 827px;
  position: absolute;
  z-index: -1;
}

.container {
  padding-left: 14%;
  padding-right: 14%;
}

@media screen and (max-width: 1000px) {
  .background {
    height: 1300px;
    background-image: none;
    background-color: $main-color;
  }
}
